.modal-card_backdrop {
  height: 100%;
  width: 100%;
  z-index: 5;
  opacity: 1;
  background-color: #1116;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.modal-card_backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: visible;
}

.modal-card {
  max-width: 335px;
  width: 100%;
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 18px;
  margin: 0 auto;
  padding: 24px 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%)scale(1);
}

@media screen and (min-width: 768px) {
