}

.slider:before {
  content: ‘’;
  height: 19px;
  width: 19px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: .5px;
  left: 1px;
}

input:checked + .slider {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

